import React, {useEffect, useState} from "react"
import {connect} from "react-redux";
import {updateJoueurState} from "../../store/actions";
import UserActionApi from "../../services/UserActionApi";
import pnjApi from "../../services/pnjApi";
import {toast} from "react-toastify";


const ActionView = (props) => {

    const [sequence, setSequence] = useState();

    useEffect(()=>{
        fetchActionData();
    }, []);

    const fetchActionData = async () => {
        const actionData = await pnjApi.getPnjAction(props.pnjId)
        setSequence(actionData);
    }

    const handleAction = async (link, params, actionId) => {
        const messageData = await UserActionApi.applyUserAction(link, params, actionId);
        toast(messageData.message);
    }

    return(
        <div className="quest-modal-body">
            <div>
                {sequence && sequence.dialogue}<br />
                {(sequence && sequence.actions.length > 0) && sequence.actions.map(action => <><button onClick={() => handleAction(action.actionLink, action.actionParams, action.actionId)} className="btn-action">{action.actionName}</button><br/> </>)}
            </div>
        </div>
    )
}

export default connect((state, ownProperties) =>{
    return {joueurState: {...state.data.joueurState}, ownProperties}
}, {updateJoueurState})(ActionView)